import React from 'react'
import {Container, Card, Form, Toast} from 'react-bootstrap'

import 'bootstrap/dist/css/bootstrap.min.css'

export default class App extends React.Component{
  constructor(props){
    super(props)
    this.state = {
      teks : "",
      pilihan : "0",
      changedTeks : "",
      copied : false
    }

    this.kotakCopyRef = React.createRef()
    this.kotakAsli = React.createRef()
    this.pilihan = React.createRef()
  }

  convertEmoji(str) {
    return str.replace(/\[e-([0-9a-fA-F]+)\]/g, (match, hex) =>
      String.fromCodePoint(Number.parseInt(hex, 16))
    )
  }

  getRandomInt(max) {
    return Math.floor(Math.random() * Math.floor(max));
  }

  onChangeTeks(e){
    this.setState(state => {state.teks = this.kotakAsli.current.value})
    this.applyChanges()
    this.forceUpdate()

  }

  onChangePilihan(e){
    this.setState(state => {state.pilihan = this.pilihan.current.value})
    this.forceUpdate()
  }

  applyChanges(){
    let teks = this.kotakAsli.current.value
    if(this.state.pilihan === "1"){
      if(teks.length !== 0){
        let text = ""
        for(let y = 0; y < teks.length; y++){
          if(teks[y] === 'O' || teks[y] === 'o'){
            text += this.convertEmoji('[e-1F913]')
          }else{
            text += teks[y]
          }
        }
        this.setState(state => {state.changedTeks = text})
      }else{
        this.setState(state => {state.changedTeks = ""})
      }
    }else if(this.state.pilihan === "2"){
      if(teks.length !== 0){
        let splices = teks.split(/\b(\s)/)
        for(let u = 0; u < splices.length; u++){
            let regex = new RegExp('^(k|K)(o|O)(k|K)(o|O)(h|H)$')
            let regex2 = new RegExp('^(k|K)(o|O)(h|H)$')
            if(regex.test(splices[u])){
              splices[u] = "K" + this.convertEmoji('[e-1F913]') + "k" + this.convertEmoji('[e-1F913]') + "h"
            }else if(regex2.test(splices[u])){
              splices[u] = "K" + this.convertEmoji('[e-1F913]') + "h"
            }
        }
        let finall = splices.join("")
        this.setState(state => {state.changedTeks = finall})
      }else{
        this.setState(state => {state.changedTeks = ""})
      }
    }
  }

  copies(e){
    this.kotakCopyRef.current.select()
    try {
      let successful = document.execCommand('copy');
      this.setState(state => {state.copied = true})
    } catch(err) {
      
    }finally{
      setTimeout(() => {
        this.setState(state => {state.copied = false})
      }, 3000)
    }
  }

  render(){
    return(
      <Container style={{padding : 80, paddingTop : 50}}>
        <Card>
          <Card.Body>
            <Card.Header as="h1" style={{textAlign : 'center'}}>Kokoh Transformer</Card.Header>
            <Card.Text>
              <p style={{textAlign : 'center'}}>Ubah setiap "O" menjadi </p>
              <Form.Group controlId="cts1">
                <Form.Label>Pilih Format </Form.Label>
                <Form.Control as="select" ref={this.pilihan} onChange={this.onChangePilihan.bind(this)}>
                  <option>---</option>
                  <option value="1">Ubah semua "O" menjadi </option>
                  <option value="2">Kokoh Mode </option>
                </Form.Control>
              </Form.Group>

              <Form.Group controlId="cts2">
                <Form.Label>Masukkan kata2 yang akan diubah menjadi </Form.Label>
                  <Form.Group controlId="cts2">
                    <Form.Control as="textarea" rows="3" onChange={this.onChangeTeks.bind(this)} ref={this.kotakAsli}/>
                  </Form.Group>
              </Form.Group>

              <Toast show={this.state.copied} style={{float: 'right'}} delay={1500} autohide>
                <Toast.Header><span>Tercopy!</span></Toast.Header>
              </Toast>              

              <Form.Group controlId="cts3">
                <Form.Label>Hasilnya ada dibawah koh,  (Tekan kotak dibawah untuk copy)</Form.Label>
                  <Form.Group controlId="cts3">
                    <Form.Control as="textarea" rows="3" readOnly ref={this.kotakCopyRef} value={this.state.changedTeks} onClick={this.copies.bind(this)}/>
                  </Form.Group>
              </Form.Group>

            </Card.Text>
          </Card.Body>
        </Card>
      </Container>
    )
  }
}